import React from 'react';
import styled from 'styled-components';
import MainTitle from '../atoms/bannerAtoms/mainTitle';
import SecondTitle from '../atoms/bannerAtoms/secondTitle';
import Button from '../atoms/bannerAtoms/button';

const BannerText = ({isPortuguese}) =>{
    return(
        <Div isPortuguese={isPortuguese}>
            <MainTitle isPortuguese={isPortuguese}/>
            <SecondTitle isPortuguese={isPortuguese}/>
            <Button isPortuguese={isPortuguese}/>
        </Div>
    );
}
const Div = styled.div`
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: ${props=>props.isPortuguese ? '250px' : '270px'};
    height: 330px;
`;
export default BannerText;