import React from 'react';
import styled from 'styled-components';
import facebook from '../../../img/face.ico'
import google from '../../../img/google.ico'
import instagram from '../../../img/insta.ico'
import youtube from '../../../img/Youtube.ico'

const List = () =>{
    return(
        <Ul>
            <Li F >Facebook</Li>
            <Li I >Instagram</Li>
            <Li Y >Youtube</Li>
            <Li G >Google e sites</Li>
        </Ul>
    );
}
const Ul = styled.ul`
    
`;
const Li = styled.li`
    font-size: 1.2rem;
    line-height: 32px;
    list-style: none;
    margin-bottom: 5px;
    position: relative;
    padding-left: 20%;

    &::before{
        content: '';
        width: 32px;
        height: 32px;
        position: absolute;
        transform: translateX(-120%);
        background-image: url(${
            props=>props.F ? facebook : 
            props.I ? instagram :
            props.Y ? youtube : google
        });
    }

`;
/* url(${
    props=>props.F ? facebook : 
    props.I ? instagram :
    props.Y ? youtube : google
}) */
export default List;