import React from 'react';
import styled from 'styled-components';

const Image = ({img, title}) =>{
    return(
        <Div>
            <Img src={img} alt={title} title={title}/>
        </Div>
    );
}
const Div = styled.div`
    grid-column: 2;
`;
const Img = styled.img`
    width: 150px;
`;
export default Image;