import React from 'react';
import styled from 'styled-components';

const StepTitle = ({children, right}) =>{
    return(
        <H3 right={right}>{children}</H3>
    );
}
const H3 = styled.h3`
    color: #EC5701;
    padding: 0 5%;
    font-size: 1.5rem;
    position: relative;
    z-index: 10;
    text-align: ${props=>props.right ? 'right' : ''}
`;
export default StepTitle;