import React from 'react';
import styled from 'styled-components';

const Text = ({children, right}) =>{
    return(
        <P right={right}><B>---</B>{children}</P>
    );
}
const P = styled.p`
    font-size: 1.1rem;
    position: relative;
    z-index: 10;
    ${props=>props.right ? 'text-align: right; font-weight: 500' : ''};
`;
const B = styled.b`
    letter-spacing: -3px;
    margin-right: 8px;
`;
export default Text;