import React from 'react';
import styled from 'styled-components';

const Button = ({isPortuguese}) =>{
    return(
        isPortuguese ? 
        <Div >VENDER MAIS AGORA</Div>
        :
        <Div >VENDER MÁS AHORA</Div>
    );
}
const Div = styled.div`
    position: absolute;
    background-color: hsl(120, 95%, 45%);
    color: aliceblue;
    top: 275px;
    Padding: 10px 15px;
    border-radius: 5px;
    box-shadow: 2px 2px 3px black;
    cursor: pointer;

    &:active{
        transform: scale(.97);
        box-shadow: 1px 1px 1px black;
    }
`;
export default Button;