import React from 'react';
import styled from 'styled-components';

const List = ({children}) =>{
    return(
        <Ul>{children.map((child, index)=>{
            return(
                <Li key={index}><P>{child}</P></Li>
            )
        })}</Ul>
    );
}
const Ul = styled.ul`
    display: flex;
    flex-direction: column;
    position: relative;
    z-index: 10;
    margin-bottom: 50px;
`;
const Li = styled.li`
    font-size: 1.1rem;
    margin: 2px;
    
`;
const P = styled.p`
    margin: 0;
    display: inline;
    background-color: #ffc100;
`;
export default List;