import React from 'react';
import styled from 'styled-components';

const Hamburguer = ({changeMenuVisibility, isVisible}) =>{
    
    const changeVisibility = () =>{
        changeMenuVisibility();
    }
    const hideMenu = () =>{
        if(!isVisible) return
        changeVisibility();
    }
    return(
        <HamburguerContainer 
            onClick={()=>changeVisibility()}
            onMouseLeave={()=>hideMenu()}
        >
            <Piece isVisible={isVisible}/>
            <Piece isVisible={isVisible} small/>
            <Piece isVisible={isVisible}/>
            <Piece isVisible={isVisible} small/>
            <Piece isVisible={isVisible}/>
        </HamburguerContainer>
    );
}
const HamburguerContainer = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
`;
const Piece = styled.div`
    border-radius: 3px;
    transition: background-color 1s;
    margin: ${props => props.small ? '0' : '3px 0'};
    height: ${props => props.small ? '3px' : '4px'};
    width: ${props => props.small ? '20px' : '35px'};
    background-color: ${props => props.isVisible ? 'hsla(198, 69%, 66%, .3)' : 'aliceblue'};
`;       
export default Hamburguer;