import React from 'react';
import styled from 'styled-components';

const List = ({list, title}) =>{
    return(
        <Div>
            <H3>{title}</H3>
            <Ul>
                {list.map((item, index)=>{
                    return(
                        <Li key={index}>{item}</Li>
                        )
                    })}
            </Ul>
        </Div>
    );
}
const Div = styled.div`
    grid-column: 1;
    grid-row: 1;
`;
const H3 = styled.h3`
    color: #EC5701;
    font-size: 1.3rem;
    text-align: center;
`;
const Ul = styled.ul`
    padding-left: 20px;
    `;
const Li = styled.li`
    color: aliceblue;
    font-size: 1.1rem;
    line-height: 25px;
`;
export default List;