import React from 'react';
import styled from 'styled-components';

const MainTitle = ({isPortuguese}) =>{
    return(
        isPortuguese ? 
        <H2>VOCÊ PODE<br/>VENDER<br/>MUITO MAIS</H2> 
        : 
        <H2>USTED PUEDE<br/>VENDER<br/>MUCHO MÁS</H2>
    );
}
const H2 = styled.h2`
    text-align: center;
    position: absolute;
    display: inline-block;
    font-size: 2.5rem;
    color: #EC5701;
    font-weight: 900;
    text-shadow: 3px 3px 5px hsla(198, 69%, 66%) ;
`;
export default MainTitle;