import React from 'react';
import styled from 'styled-components';
import img from '../../../img/pc.webp'

const SubTitle = ({text}) =>{
    return(
        <div>
            {(text==='Facebook') && 
                <Div>
                    <Img src={img} alt="Icon" />
                    <H3><B>Facebook</B> Ads</H3>
                </Div> }
            {(text==='Google') && 
                <Div Google>
                <H3><B1>G</B1><B2>o</B2><B3>o</B3><B1>g</B1><B4>l</B4><B2>e</B2> Ads</H3>
                <Img src={img} alt="Icon" />
            </Div> }
        </div>
    );
}
const Div = styled.div`
    display: flex;
    align-items: center;
    justify-content: ${props=>props.Google ? 'flex-end' : ''};
`;
const Img = styled.img`
    height: 25px;
    margin: 0 10px;
`;
const H3 = styled.h3`
    margin: 10px 0;
    font-size: 2.2rem;
    color: grey;
`;
const B = styled.b`
    color: #3b5998;
`;
const B1 = styled.b`
    color: #4285F4;
`;
const B2 = styled.b`
    color: #DB4437;
`;
const B3 = styled.b`
    color: #F4B400;
`;
const B4 = styled.b`
    color: #0F9D58;
`;
export default SubTitle;