import React from 'react';
import styled from 'styled-components';

const ImgContainer = ({img, title}) =>{
    return(
        <Div>
            {(title==='Facebook') && <H3 Facebook >Facebook</H3>}
            {(title==='Instagram') && <H3 Instagram>Instagram</H3>}
            {(title==='Google') && <H3><B1>G</B1><B2>o</B2><B3>o</B3><B1>g</B1><B4>l</B4><B2>e</B2></H3>}
            <Img src={img} alt={title} title={title}/>
        </Div>
    );
}
const Div = styled.div`

`;
const H3 = styled.h3`
    font-size: 2rem;
    margin: 0;
    text-align: center;
    color: ${props=> props.Facebook ? '#3b5998' :
                    props.Instagram ? '#E1306C' : ''};
`;
const Img = styled.img`
    margin-bottom: 20px;
`;
const B1 = styled.b`
    color: #4285F4;
`;
const B2 = styled.b`
    color: #DB4437;
`;
const B3 = styled.b`
    color: #F4B400;
`;
const B4 = styled.b`
    color: #0F9D58;
`;
export default ImgContainer;