import React from 'react';
import styled from 'styled-components';
import { Link } from "react-router-dom";
import world from '../../../img/world.png';

const Menu = ({isVisible, isPortuguese, setLanguaje}) =>{
    const handleClick = (bool) => setLanguaje(bool)
    return(
        <MenuContainer isVisible={isVisible}>
            <Ul>
                <Li><StyledLink to="#">{isPortuguese ? 'O que a GMC Faz?':'Qué Hacemos?'}</StyledLink></Li>
                <Li><StyledLink to="#">Método GMC</StyledLink></Li>
                <Li><StyledLink to="#">{isPortuguese ? 'Perguntas Frequentes':'Preguntas Frecuentes'}</StyledLink></Li>
                <Li><StyledLink to="#">{isPortuguese ? 'Contato':'Contacto'}</StyledLink></Li>
                <Li>
                    <SelectLang 
                        isPortuguese={isPortuguese}
                        onClick={()=>handleClick(true)}
                    >
                        PR
                    </SelectLang>
                    <SelectLang 
                        isPortuguese={!isPortuguese}
                        onClick={()=>handleClick(false)}
                    >
                        ES
                    </SelectLang>
                </Li>
            </Ul>
        </MenuContainer>
    );
}
const MenuContainer = styled.div`
    top: 55px;
    right: 0;
    position: absolute;
    z-index:1000;
    border-radius: 5px;
    background-color: hsla(198, 69%, 66%, .9);
    transform: translateX(${props=>props.isVisible ? '0%' : '110%'});
    transition: transform ${props=>props.isVisible ? '1s ease-out' : '1s .3s '};
`;
const Ul = styled.ul`
    margin: 0;
    padding: 0;
    list-style: none;
`;
const Li = styled.li`
    width: 220px;
    height: 40px;
`;
const StyledLink = styled(Link)`
    width: 100%;
    height: 100%;
    font-size: 1.3rem;
    line-height: 35px;
    text-decoration: none;
    color: aliceblue;
    display: block;
    padding-left: 10px;
    
    &:hover{
        background-color: hsla(198, 69%, 66%, 1);
    }
`;
const SelectLang = styled.div`
    display: inline-block;
    background-image: url(${props=>props.isPortuguese ? world : ''});
    background-repeat: no-repeat;
    background-size: 100%;
    color: aliceblue;
    font-size: 1.3rem;
    font-weight: 700;
    line-height: 35px;
    text-align: center;
    margin: 0 10px;
    width: 33px;
`;
export default Menu;