import React from 'react';
import styled from 'styled-components';

const Arrow = (props) =>{
    const {} = props
    return(
        <Arrows>
            <Div></Div>
            <Div></Div>
        </Arrows>
    );
}
const Arrows = styled.div`
    width: 0;
    margin: 0 auto;
`;
const Div = styled.div`
    position: relative;
    margin: 0 auto;
    width: 0;
    height: 25px;
    background-color: grey;
    
    &:before,
    &:after{
        content: '';
        position: absolute;
        width: 30px;
        height: 10px;
        border-radius: 3px;
        background-color: #EC5701;
        transform: rotate(45deg);
        left: -22px;
        
    }
    &:after{
        transform: rotate(-45deg);
        left: -8px;
    }

`;
